import { Cell, Stepper } from "@taroify/core"
import * as React from "react"
import Block from "../../../components/block"
import Page from "../../../components/page"
import "./index.scss"

export default function StepperDemo() {
  return (
    <Page title="Stepper " className="stepper-demo">
      <Block variant="card">
        <Cell title="">
          <Stepper />
        </Cell>
        <Cell title="">
          <Stepper step={2} />
        </Cell>
        <Cell title="">
          <Stepper min={5} max={8} />
        </Cell>
        <Cell title="">
          <Stepper disabled />
        </Cell>
        <Cell title="">
          <Stepper>
            <Stepper.Button />
            <Stepper.Input disabled />
            <Stepper.Button />
          </Stepper>
        </Cell>
        <Cell title="">
          <Stepper precision={2} />
        </Cell>
        <Cell align="center" title="">
          <Stepper size="32">
            <Stepper.Button />
            <Stepper.Input disabled width="40" />
            <Stepper.Button />
          </Stepper>
        </Cell>
        <Cell title="">
          <Stepper shape="round" size="22" />
        </Cell>
      </Block>
    </Page>
  )
}
